import React from 'react';
import ScrollIntoView from 'react-scroll-into-view';
import {VscArrowDown} from 'react-icons/vsc';

function About() {
    return(
        <div className='max-w-4xl mt-20 mx-auto'>
            <p className='text-2xl text-black md:text-4xl font-bold text-center'>
                Hi! 
            </p>
            <p className='text-base sm:text-xl text-center text-gray-600 leading-relaxed mt-4'>
                I'm a 13 Years Old Full-Stack FE and BE Developer. Im active at coding community WPU (Web Programming UNPAS), I love programming and photography. Also working on open source project and freelancing.
            </p>
        
        <ScrollIntoView selector = "#skills">
            <div className='mx-auto p-20'>
                <VscArrowDown className='animate-bounce mx-auto text-3xl text-blue-500' />
            </div>
        </ScrollIntoView>


        </div>
    )
}

export default About;