import React from 'react';
import Card from './assets/compo/card'
import About from './assets/compo/about'
import Footer from './assets/compo/footer'
import Skills from './assets/compo/skill'
import Contact from './assets/compo/contact'
import Aos from 'aos';
import { useEffect } from 'react';
import 'aos/dist/aos.css';

function App() 
{
  useEffect(() => {
    Aos.init({
      once: true,
    })
  })

  return (
    <div className='min-h-screen py-10 px-3 sm:px-5 bg-gray-100'>
      <div data-aos='fade-down' data-aos-duration='800'>
      <Card />
      </div>
      <div data-aos='fade-up' data-aos-duration='800' data-aos-delay='400'>
      <About />
      </div>
      <div data-aos='fade-right' data-aos-duration='800' data-aos-delay='800'>
      <Skills />
      </div>
      <Contact/>
      <Footer />
    </div>
  );
}

export default App;
