import React from 'react';
import profile from '../img/unnamed.png';
import {FaGithub, FaSpotify, FaInstagram} from 'react-icons/fa';
import {MdOutlineEmail} from 'react-icons/md';

function Card() {
    return(
        <div className='w-full'>
            <div className='flex flex-col justify-center max-w-xs mx-auto bg-white shadow-x1 rounded-xl p-5'>
                <div className=''>
                    <img className='w-32 mx-auto shadow-xl rounded-full drop-shadow-sm' src={profile} alt='profile face' />
                </div>
                <div className="text-center mt-5">
                    <p className='text-xl sm:text-2xl text-gray-900'>
                        Lintang Segoro Pinasih
                    </p>
                    <p className='text-xs sm:text-base text-gray-600 pt-2 pb-4 px-5 w-auto inline-block border-b-2'>
                        Full-Stack Developer
                    </p>

                    <div className='flex align-center justify-center mt-4'>
                
                <a className="text-xl m-1 p-1 sm:m-2 sm:p-2 text-gray-800 hover:bg-gray-800 rounded-full hover:text-white transition-colors duration-300" target="_blank" href="https://github.com/LintangDev">
                    <FaGithub />
                    <span class='sr-only'>Github</span>
                </a>
                <a className="text-xl m-1 p-1 sm:m-2 sm:p-2 text-gray-800 hover:bg-green-800 rounded-full hover:text-white transition-colors duration-300" target="_blank" href="https://open.spotify.com/user/r2828pz36s9csm01gqlvxz8z9?si=f4dab6bc4f884aed">
                    <FaSpotify />
                    <span class='sr-only'>Spotify</span>
                </a>
                <a className="text-xl m-1 p-1 sm:m-2 sm:p-2 text-gray-800 hover:bg-purple-800 rounded-full hover:text-white transition-colors duration-300" target="_blank" href="https://instagram.com/lintaangs">
                    <FaInstagram />
                    <span class='sr-only'>Instagram</span>
                </a>
                <a className="text-xl m-1 p-1 sm:m-2 sm:p-2 text-gray-800 hover:bg-red-800 rounded-full hover:text-white transition-colors duration-300" target="_blank" href="mailto:lintangch15@gmail.com">
                    <MdOutlineEmail />
                    <span class='sr-only'>Email</span>
                </a>

                </div>
            </div>
    </div>
        </div>
    )
}

export default Card;