import React from 'react';
import {SiJavascript, SiReact, SiHtml5, SiCss3, SiPhp} from 'react-icons/si'
import {FaNodeJs} from 'react-icons/fa'


function Skills() {
    return(
        <div className='max-w-4xl mx-auto justify-center py-12' id='skills'>
            <p className='text-2xl text-black text-center md:text-4xl pt-4 font-bold'>
            Languages and Tools
            </p>
                <div className='flex flex-wrap justify-center pt-2'>
            <div className='flex flex-col w-40 p-10 m-6 overflow-hidden bg-white shadow-2xl rounded-xl sm:w-52'>
                <SiJavascript color="#f1da4e" className='mx-auto text-4xl'/>
                <p className='mt-6 text-xl sm:text-2xl font-semibold text-center'>Javascript</p>
            </div>
            <div className='flex flex-col w-40 p-10 m-6 overflow-hidden bg-white shadow-2xl rounded-xl sm:w-52'>
                <SiReact color="#62dafc" className='mx-auto text-4xl'/>
                <p className='mt-6 text-xl sm:text-2xl font-semibold text-center'>React</p>
            </div>
            <div className='flex flex-col w-40 p-10 m-6 overflow-hidden bg-white shadow-2xl rounded-xl sm:w-52'>
                <SiHtml5 color="#e54d26" className='mx-auto text-4xl'/>
                <p className='mt-6 text-xl sm:text-2xl font-semibold text-center'>Html</p>
            </div>
            <div className='flex flex-col w-40 p-10 m-6 overflow-hidden bg-white shadow-2xl rounded-xl sm:w-52'>
                <SiCss3 color="#264ee4" className='mx-auto text-4xl'/>
                <p className='mt-6 text-xl sm:text-2xl font-semibold text-center'>Css</p>
            </div>
            <div className='flex flex-col w-40 p-10 m-6 overflow-hidden bg-white shadow-2xl rounded-xl sm:w-52'>
                <FaNodeJs color="#679e64" className='mx-auto text-4xl'/>
                <p className='mt-6 text-xl sm:text-2xl font-semibold text-center'>Nodejs</p>
            </div>
            <div className='flex flex-col w-40 p-10 m-6 overflow-hidden bg-white shadow-2xl rounded-xl sm:w-52'>
                <SiPhp color="#777bb3" className='mx-auto text-4xl'/>
                <p className='mt-6 text-xl sm:text-2xl font-semibold text-center'>Php</p>
            </div>

                </div>
        </div>
    )
}

export default Skills;